// Cloudflare Worker  ( API )
const DEEPSEEK_API_URL = 'https://meiban-deepseek-proxy.kidyuan.workers.dev';
// API key  Cloudflare Secrets Store 

// 
function buildAstrologyContext(userData, calculationResults, language = 'zh') {
  const languagePrompts = {
    zh: {
      systemRole: "",
      instructions: "200-300",
      birthInfo: "",
      calculationInfo: ""
    },
    ja: {
      systemRole: "",
      instructions: "200-300",
      birthInfo: "",
      calculationInfo: ""
    },
    en: {
      systemRole: "You are a senior astrologer and numerology expert, specializing in traditional Chinese BaZi (Eight Characters) and modern Western astrology. Based on the user's detailed birth chart information, Five Elements theory, Heavenly Stems and Earthly Branches, and precise planetary positions, provide professional, accurate, and insightful personalized astrological analysis.",
      instructions: "Please respond in clear, accessible English. Avoid overly academic language, maintain a warm and professional tone, and offer practical advice. Keep responses between 200-300 words.",
      birthInfo: "User Basic Information",
      calculationInfo: "Detailed Chart Data"
    }
  };

  const langConfig = languagePrompts[language] || languagePrompts.zh;
  
  let context = `${langConfig.systemRole}\n\n${langConfig.instructions}\n\n`;
  
  if (userData) {
    context += `${langConfig.birthInfo}:\n`;
    context += `- : ${userData.birthdate}\n`;
    context += `- : ${userData.birthHour}:${userData.birthMinute}\n`;
    context += `- : ${userData.birthplace}\n`;
    context += `- : ${userData.gender === 'male' ? '' : ''}\n\n`;
  }
  
  if (calculationResults) {
    context += `${langConfig.calculationInfo}:\n`;
    
    // 
    if (calculationResults.eightCharacters) {
      context += `:\n`;
      context += `- : ${calculationResults.eightCharacters.year?.heavenlyStem}${calculationResults.eightCharacters.year?.earthlyBranch}\n`;
      context += `- : ${calculationResults.eightCharacters.month?.heavenlyStem}${calculationResults.eightCharacters.month?.earthlyBranch}\n`;
      context += `- : ${calculationResults.eightCharacters.day?.heavenlyStem}${calculationResults.eightCharacters.day?.earthlyBranch}\n`;
      context += `- : ${calculationResults.eightCharacters.hour?.heavenlyStem}${calculationResults.eightCharacters.hour?.earthlyBranch}\n\n`;
    }
    
    // 
    if (calculationResults.astrologyPositions) {
      context += `:\n`;
      if (calculationResults.astrologyPositions.sun) {
        context += `- : ${calculationResults.astrologyPositions.sun.sign} ${calculationResults.astrologyPositions.sun.degree}${calculationResults.astrologyPositions.sun.minute}'\n`;
      }
      if (calculationResults.astrologyPositions.moon) {
        context += `- : ${calculationResults.astrologyPositions.moon.sign} ${calculationResults.astrologyPositions.moon.degree}${calculationResults.astrologyPositions.moon.minute}'\n`;
      }
      if (calculationResults.astrologyPositions.ascendant) {
        context += `- : ${calculationResults.astrologyPositions.ascendant.sign} ${calculationResults.astrologyPositions.ascendant.degree}${calculationResults.astrologyPositions.ascendant.minute}'\n`;
      }
      context += '\n';
    }
    
    // 
    if (calculationResults.elements && calculationResults.elements.length > 0) {
      context += `: ${calculationResults.elements.join('')}\n\n`;
    }
  }
  
  return context;
}

//  ( Cloudflare Worker  DeepSeek)
export async function handleChatRequest(messages, userContext) {
  try {

    const { userData, calculationResults, language = 'zh' } = userContext;
    
    // 
    const systemContext = buildAstrologyContext(userData, calculationResults, language);
    
    // 
    const fullMessages = [
      {
        role: 'system',
        content: systemContext
      },
      ...messages
    ];
    
    //  Cloudflare Worker 
    console.log('Calling DeepSeek API via Cloudflare Worker with messages:', fullMessages);
    
    const response = await fetch(DEEPSEEK_API_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        model: 'deepseek-chat',
        messages: fullMessages,
        temperature: 0.7,
        max_tokens: 2048,
        top_p: 0.9,
        stream: true, // 
      }),
    });

    console.log('Cloudflare Worker Response status:', response.status);
    console.log('Cloudflare Worker Response headers:', response.headers);

    if (!response.ok) {
      const errorText = await response.text();
      console.error('Cloudflare Worker Error response:', errorText);
      let errorData;
      try {
        errorData = JSON.parse(errorText);
      } catch {
        errorData = { error: { message: errorText } };
      }
      throw new Error(`API Proxy Error: ${response.status} - ${errorData.error?.message || response.statusText}`);
    }

    //  Vercel AI SDK 
    return {
      textStream: createTextStream(response)
    };
    
  } catch (error) {
    console.error('AI Chat API Error:', error);
    throw error;
  }
}

// 
async function* createTextStream(response) {
  const reader = response.body.getReader();
  const decoder = new TextDecoder();
  let buffer = '';
  let hasYieldedContent = false;
  
  console.log('...');
  
  try {
    while (true) {
      const { done, value } = await reader.read();
      if (done) {
        console.log('hasYieldedContent:', hasYieldedContent);
        break;
      }
      
      const chunk = decoder.decode(value, { stream: true });
      buffer += chunk;
      console.log(':', chunk);
      
      // 
      const lines = buffer.split('\n');
      buffer = lines.pop(); // 
      
      for (const line of lines) {
        const trimmedLine = line.trim();
        if (!trimmedLine) continue; // 
        
        console.log(':', trimmedLine);
        
        if (trimmedLine.startsWith('data: ')) {
          const data = trimmedLine.slice(6); //  "data: " 
          
          console.log('DeepSeek Stream Data:', data);
          
          if (data === '[DONE]') {
            console.log('Stream finished');
            return;
          }
          
          try {
            const parsed = JSON.parse(data);
            console.log('Parsed response:', JSON.stringify(parsed, null, 2));
            
            const content = parsed.choices?.[0]?.delta?.content;
            if (content) {
              console.log('Content chunk:', content);
              hasYieldedContent = true;
              yield content;
            } else if (parsed.choices?.[0]?.delta) {
              console.log('Delta without content:', parsed.choices[0].delta);
            }
          } catch (e) {
            console.error('Failed to parse JSON:', data, e);
            // 
            if (data && data !== 'null' && !data.startsWith('{')) {
              console.log(':', data);
              hasYieldedContent = true;
              yield data;
            }
          }
        } else if (trimmedLine.startsWith('{')) {
          //  "data: " 
          try {
            const parsed = JSON.parse(trimmedLine);
            console.log('JSON:', JSON.stringify(parsed, null, 2));
            
            const content = parsed.choices?.[0]?.delta?.content;
            if (content) {
              console.log('Content chunk ():', content);
              hasYieldedContent = true;
              yield content;
            }
          } catch (e) {
            console.error('JSON:', trimmedLine, e);
          }
        }
      }
    }
    
    // buffer
    if (buffer.trim()) {
      console.log('buffer:', buffer.trim());
    }
    
  } catch (error) {
    console.error('Stream processing error:', error);
    throw error;
  } finally {
    reader.releaseLock();
  }
}

